.App {
  background-color: #e5e3eb;
  min-height: 100vh;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.photo {
  border-radius: 50%;
  width: 300px;
}

.congratContainer {
  padding-top: 70px;
  height: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
}

.congrat {
  font-size: 30px;
  font-weight: 600;
  font-family: sans-serif;
  line-height: 50px;
}

.congrat-2 {
  width: 350px;
  font-size: 25px;
  font-weight: 400;
  font-family: sans-serif;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

.present {
  width: 250px;
  transition: 0.5s;
}

.present:hover {
  cursor: pointer;
  transform: scale(1.2);
}

.presentContainer {
  animation: pulse 2s infinite;
  text-align: center;
  color: rgba(0, 0, 0, 0.4);
}

.cakeImg {
  animation: pulse 2s infinite;
}

.cake {
  font-size: 30px;
}